}


.navbar {
  background-color: black;
  justify-content: center;
}

.navbar.center .navbar-inner {
  text-align: center;
}
.navbar.center .navbar-inner .nav {
  float: none;
  display:inline-block;
  vertical-align: top;  
}

.nav-brand {
  margin: auto;
  color: white !important;
  font-size: 30px !important;
  font-weight: 700;
  text-transform: uppercase;

}

.nav-link{
  color: white !important;
  text-transform: uppercase;
}

