  background-color: lightgrey;
  margin: 0;
}
.jumbotron-subheading {
  padding-top: 5px;
  font-size: 1.25rem;
}
.page-section { 
  padding: 6rem 0;
}


#about {
  background-color: white;
  margin: 0;
}

 

